"use client";

import { useState } from 'react';
import { motion } from 'framer-motion';
import { ArrowUpRight, Heart, Eye } from 'lucide-react';
import Link from 'next/link';

export interface ProjectType {
    _id: string;
    title: string;
    description: string;
    image: string;
    tags: string[];
    link?: string;
    likes?: number;
    views?: number;
}

const Projects = ({ projects, showViewCounter = true }: { projects: ProjectType[], showViewCounter?: boolean }) => {
    // Local state for optimistic updates
    const [projectsState, setProjectsState] = useState(projects);
    const [likedProjects, setLikedProjects] = useState<string[]>([]);

    const handleLike = async (id: string, e: React.MouseEvent) => {
        e.preventDefault();
        e.stopPropagation();

        if (likedProjects.includes(id)) return; // Prevent spamming

        // Optimistic update
        setProjectsState(prev => prev.map(p =>
            p._id === id ? { ...p, likes: (p.likes || 0) + 1 } : p
        ));
        setLikedProjects(prev => [...prev, id]);

        try {
            await fetch(`/api/projects/${id}/like`, { method: 'POST' });
        } catch (error) {
            // Revert on error (optional, skipping for simplicity)
        }
    };

    const displayProjects = projectsState.length > 0 ? projectsState : [/*...*/];

    return (
        <section id="projects" className="py-24 px-6 bg-background">
            <div className="max-w-7xl mx-auto">
                <motion.h2
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    className="font-display text-4xl md:text-5xl font-bold mb-16 text-primary text-center md:text-left"
                >
                    Selected <span className="text-accent">Works</span>
                </motion.h2>

                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                    {displayProjects.map((project, index) => (
                        <div key={index} className="group block relative">
                            <motion.div
                                initial={{ opacity: 0, y: 20 }}
                                whileInView={{ opacity: 1, y: 0 }}
                                transition={{ delay: index * 0.1 }}
                                viewport={{ once: true }}
                            >
                                <a href={project._id ? `/projects/${project._id}` : (project.link || '#')} className="block relative aspect-[4/3] rounded-2xl overflow-hidden mb-6 border border-border group-hover:border-accent/30 transition-colors cursor-pointer">
                                    <div className="absolute inset-0 bg-black/20 group-hover:bg-black/0 transition-colors z-10" />
                                    <img
                                        src={project.image}
                                        alt={project.title}
                                        className="object-cover w-full h-full transform group-hover:scale-105 transition-transform duration-700"
                                    />
                                    <div className="absolute top-4 right-4 z-20 flex gap-2">
                                        {/* View Count */}
                                        {showViewCounter && project.views !== undefined && (
                                            <div className="bg-black/50 backdrop-blur-md p-2 rounded-full flex items-center gap-1 text-white text-xs">
                                                <Eye size={14} />
                                                <span>{project.views}</span>
                                            </div>
                                        )}
                                        {/* Like Button */}
                                        <button
                                            onClick={(e) => project._id && handleLike(project._id, e)}
                                            className={`bg-black/50 backdrop-blur-md p-2 rounded-full transition-all hover:scale-110 active:scale-95 flex items-center gap-1 text-xs ${likedProjects.includes(project._id || '') ? 'text-red-500' : 'text-white hover:text-red-400'}`}
                                        >
                                            <Heart size={14} className={likedProjects.includes(project._id || '') ? 'fill-current' : ''} />
                                            {project.likes && project.likes > 0 && (
                                                <span>{project.likes}</span>
                                            )}
                                        </button>

                                        <div className="bg-black/50 backdrop-blur-md p-2 rounded-full opacity-0 group-hover:opacity-100 transition-opacity translate-y-2 group-hover:translate-y-0">
                                            <ArrowUpRight className="text-white w-5 h-5" />
                                        </div>
                                    </div>
                                </a>

                                <h3 className="text-xl font-bold text-primary mb-2 group-hover:text-accent transition-colors">
                                    <a href={project._id ? `/projects/${project._id}` : (project.link || '#')}>{project.title}</a>
                                </h3>
                                <p className="text-secondary text-sm mb-4 line-clamp-2">
                                    {project.description}
                                </p>
                                <div className="flex flex-wrap gap-2">
                                    {project.tags.map(tag => (
                                        <span key={tag} className="text-xs font-medium px-2 py-1 rounded bg-surface text-secondary border border-border">
                                            {tag}
                                        </span>
                                    ))}
                                </div>
                            </motion.div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default Projects;
